<template><div class="markdown-body"><h1>头像</h1>
<p><code v-pre="">mu-avatar</code> 用于显示用户头像、简短的问题和图标。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>设置 <code v-pre="">size</code> 属性，可以改变 <code v-pre="">mu-avatar</code> 的大小</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container style=\&quot;padding: 16px 0;\&quot;&gt;\n  &lt;mu-flex&gt;\n    &lt;mu-flex&gt;\n      改变大小：\n    &lt;/mu-flex&gt;\n    &lt;mu-flex fill style=\&quot;padding: 0 16px;\&quot;&gt;\n      &lt;mu-slider v-model=\&quot;size\&quot; :min=\&quot;36\&quot; :step=\&quot;1\&quot; :max=\&quot;200\&quot;&gt;\n    &lt;/mu-slider&gt;&lt;/mu-flex&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-row justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-avatar :size=\&quot;size\&quot;&gt;\n      &lt;img src=\&quot;../../assets/images/uicon.jpg\&quot;&gt;\n    &lt;/mu-avatar&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      size: 56\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container style="padding: 16px 0;">
  <mu-flex>
    <mu-flex>
      改变大小：
    </mu-flex>
    <mu-flex fill="" style="padding: 0 16px;">
      <mu-slider v-model="size" :min="36" :step="1" :max="200">
    </mu-slider></mu-flex>
  </mu-flex>
  <mu-row justify-content="center">
    <mu-avatar :size="size">
      <img src="../../assets/images/uicon.jpg">
    </mu-avatar>
  </mu-row>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding: 16px 0;"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span>&gt;</span>
      改变大小：
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">fill</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding: 0 16px;"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"size"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"36"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"200"</span>/&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-row</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"size"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"../../assets/images/uicon.jpg"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-row</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">size</span>: <span class="hljs-number">56</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="xian-shi-wen-zi-huo-zhe-tu-biao"><a class="header-anchor" href="#xian-shi-wen-zi-huo-zhe-tu-biao" aria-hidden="true">¶</a> 显示文字或者图标</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;around\&quot;&gt;\n    &lt;mu-avatar color=\&quot;indigo\&quot;&gt;\n      &lt;mu-icon value=\&quot;account_circle\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-avatar&gt;\n    &lt;mu-avatar&gt;\n      &lt;img src=\&quot;../../assets/images/uicon.jpg\&quot;&gt;\n    &lt;/mu-avatar&gt;\n    &lt;mu-avatar text-color=\&quot;orange200\&quot; color=\&quot;pink400\&quot;&gt;\n      &lt;mu-icon value=\&quot;folder\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-avatar&gt;\n    &lt;mu-avatar color=\&quot;teal\&quot;&gt;\n      K\n    &lt;/mu-avatar&gt;\n      &lt;mu-avatar color=\&quot;red\&quot;&gt;\n      L\n    &lt;/mu-avatar&gt;\n    &lt;mu-avatar text-color=\&quot;deepOrange300\&quot; color=\&quot;purple500\&quot;&gt;\n      MU\n    &lt;/mu-avatar&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="around">
    <mu-avatar color="indigo">
      <mu-icon value="account_circle"></mu-icon>
    </mu-avatar>
    <mu-avatar>
      <img src="../../assets/images/uicon.jpg">
    </mu-avatar>
    <mu-avatar text-color="orange200" color="pink400">
      <mu-icon value="folder"></mu-icon>
    </mu-avatar>
    <mu-avatar color="teal">
      K
    </mu-avatar>
      <mu-avatar color="red">
      L
    </mu-avatar>
    <mu-avatar text-color="deepOrange300" color="purple500">
      MU
    </mu-avatar>
  </mu-flex>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"around"</span> &gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"indigo"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"account_circle"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"../../assets/images/uicon.jpg"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">text-color</span>=<span class="hljs-string">"orange200"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"pink400"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"folder"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"teal"</span>&gt;</span>
      K
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"red"</span>&gt;</span>
      L
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-avatar</span> <span class="hljs-attr">text-color</span>=<span class="hljs-string">"deepOrange300"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"purple500"</span>&gt;</span>
      MU
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-avatar</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="avatar-props"><a class="header-anchor" href="#avatar-props" aria-hidden="true">¶</a> Avatar Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>组件的背景色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>text-color</td>
<td>文字的颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>size</td>
<td>组件大小</td>
<td>Number/String</td>
<td>—</td>
<td>48</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      size: 56
    }
  }
}
</script>


